body {
  text-align: center;
  text-transform: uppercase;
  background-color: whitesmoke;
}

a {
  padding-top: 1rem;
  display: block;
}

h1 {
  font-size: 2rem;
}

.questionText {
  font-size: 3rem;
}

.clickable {
  cursor: pointer;
}

.assetTable {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 5rem;
}

.assetImg {
  height: 2rem;
  width: 2rem;
}

.navContainer {
  position: fixed;
  top: 0;
  left: 0;
}

.settingsContainer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: whitesmoke;
}

.avatar {
  background-color: whitesmoke;
  margin: 0.2rem;
  padding: 0.2rem;
  height: 3rem;
  width: 3rem;
}

.settingsContainer > button {
  margin-top: 4rem;
  width: 100%;
  margin: 0.25rem;
  padding: 0.25rem;
}

.correct {
  background-color: lightgreen;
}

.incorrect {
  background-color: indianred;
}

.icon {
  height: 100%;
  width: 100%;
}

.qIcon {
  padding-top: 1rem;
  height: 3rem;
  width: 3rem;
}

.selected {
  /*filter: drop-shadow(0 0 0.75rem dodgerblue)*/
  background-color: lightgray;
}

.selectedBtn {
  background-color: lightgray;
}

.submitBtn {
  margin-top: 3rem;
  height: 3rem;
  width: 50%;
}

.answerWrapperTextToAudioImg > div {
  background-color: whitesmoke;
  width: 100%;
  padding: 0.2rem;
  margin: 0.2rem;
  border: grey 1px solid;
}

.answerWrapperTextToAudioImg > div > h2 {
  font-size: 1.5rem;
  align-self: center;
}

.answerWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 30rem;
}

.answerWrapper > div {
  border-radius: 1rem;
  background-color: whitesmoke;
  height: 10rem;
  width: 10rem;
  margin: 1rem;
}

@media only screen and (max-width: 600px) {
  .submitBtn {
    position: fixed;
    bottom: 30%;
    left: 0;
    right: 0;
    width: 100%;
    height: 4rem;
  }

  .answerWrapper {
    max-width: 30rem;
  }

  .answerWrapper > div {
    height: 10rem;
    width: 10rem;
  }

  .answerWrapper > div > h2 {
    font-size: 2rem;
  }
}